const onFileSelected = (files: FileList) => {
  const file = files[0];
  if (file === undefined) {
    return;
  }
  const reader = new FileReader();
  reader.readAsArrayBuffer(file);
  reader.onload = () => {
    console.log(reader.result);
  };
};

document.addEventListener("drop", function (e) {
  e.preventDefault();
  if (!e.dataTransfer) {
    return;
  }
  onFileSelected(e.dataTransfer.files);
});
